//Hero section styles
.hero-section {
  margin-top: 100px;
  background-image: url(../../img/hero-bg.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: left top;
  background-attachment: fixed;
  position: relative;

  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    // background-color: $black;
    // opacity: 0.2;
    height: 100%;
    width: 100%;
  }

  &__container {
    padding: 78px 0;
    text-align: center;
    color: $white;
    position: relative;
    z-index: 100;
  }

  &__title {
    font-size: 70px;
    color: $white;
    margin-bottom: 24px;
  }

  &__descr {
    max-width: 78%;
    width: 100%;
    margin: 0 auto;
    font-size: 32px;
    line-height: 1.2;
    margin-bottom: 48px;
  }

  &__links {
    display: flex;
    align-items: center;
    justify-content: center;

    .link {
      padding: 6px 18px;
      background-color: #ef181e;
      color: $white;
      border-color: #ef181e;
      border-style: solid;
      border-width: 1px;
      border-radius: 2px;
      text-decoration: none;
      font-size: 24px;
      font-weight: 500;
      margin: 0 12px;
      transition: 0.2s linear;

      &:hover {
        background-color: $white;
        color: #ef181e;
        border-color: #ef181e;
      }
    }
  }
}

// Info section styles
.info-section {
  padding: 54px 0;

  &__container {}

}

// Table info elem style
.table-info {
  max-width: 80%;
  width: 100%;
  margin: 0 auto;

  &__holder {}

  &__row {
    display: flex;
  }

  &__time {
    max-width: 24%;
    width: 100%;
    border: 1px solid #c6c6c6;
    padding: 10px;
  }

  &__details {
    max-width: 76%;
    width: 100%;
    border: 1px solid #c6c6c6;
    padding: 10px 18px;
  }

  &__marked {
    font-size: 20px;
    font-weight: 400;
    text-align: center;
    padding: 12px;
    border-bottom: 1px solid #c6c6c6;
    border-right: 1px solid #c6c6c6;
    border-left: 1px solid #c6c6c6;
  }

  .info-day {
    font-size: 20px;
    margin: 0 0 4px;
  }

  .info-time {
    font-size: 18px;
  }

  .info-topic {
    font-size: 24px;
  }

  .info-descr {
    font-size: 18px;
  }
}

// Event section style
.event-section {
  padding: 54px 0;

  &__container {}

}

// Accrodion elem style
.accordion-block {
  &__list {
    list-style: none;
  }

  &__item {
    position: relative;

    &.is-open {

      .accordion-block__ctn {
        padding-top: 24px;
        padding-bottom: 24px;
        background-color: #f3f7ff;
        border-color: #c6c6c6;
        height: auto;
        opacity: 1;
      }

      .accordion-block__btn {
        border-bottom-color: $white;
        border-radius: 4px 4px 0 0;
        border-color: #495fdf;

        &:after {
          transform: translate(0, -50%) rotate(45deg);
        }
      }
    }

    &:after {
      content: '';
      position: absolute;
      left: 0;
      right: 0;
      bottom: -8px;
      background-color: $white;
      height: 8px;
    }

    &:not(:last-child) {
      margin-bottom: 8px;
    }
  }

  &__btn {
    text-align: left;
    position: relative;
    display: block;
    width: 100%;
    background-color: $white;
    border-width: 1px;
    border-style: solid;
    border-color: #c6c6c6;
    border-radius: 4px 4px 4px 4px;
    padding: 24px 20px 24px 20px;
    transition: all 0.2 linear;

    &:hover {
      border-color: #495fdf;
      background-color: #f3f7ff;

      &:after {
        opacity: 0.7;
      }
    }

    &:after {
      content: '';
      position: absolute;
      right: 20px;
      top: 50%;
      transform: translate(0, -50%) rotate(-45deg);
      width: 12px;
      height: 12px;
      opacity: 1;
      border-left-color: transparent;
      border-top-color: transparent;
      border-right-color: #2c3e50;
      border-bottom-color: #2c3e50;
      border-style: solid;
      transform-origin: center;
      transition: all 0.3s;
      transition-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1);
    }
  }

  &__title {
    font-size: 24px;
    font-weight: 500;
    line-height: 1.33;
    text-align: left;
    color: #1a1a1a;
  }

  &__ctn {
    border-width: 1px;
    border-style: solid;
    border-color: $transp;
    font-size: 16px;
    border-radius: 0px 0px 4px 4px;
    padding-top: 0;
    padding-bottom: 0;
    padding-left: 20px;
    padding-right: 20px;
    background-color: #fff;
    height: 0;
    opacity: 0;
    transition: padding-top 0.1s ease-in,
      padding-bottom 0.1s ease-in, border-color 0.1s linear,
      background-color 0.3s linear,
      opacity 0.1s linear;

    p {

      &:last-child {
        margin: 0;
      }
    }
  }
}