.footer {
  background-color: $black;
  color: $white;
  padding: 44px 14px;

  &__container {}

  &__contacts {
    color: $white;
    display: flex;
    justify-content: space-between;

    a {
      color: $white;
      font-size: 24px;
      text-decoration: none;
      position: relative;

      &:hover {
        &:after {
          width: calc(100% - 24px);
        }
      }

      &:after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 24px;
        width: 0;
        height: 1px;
        background-color: $white;
        transition: 0.2s linear;
      }
    }

    p {
      font-size: 32px;
      font-weight: 600;
      color: #ecf0f1;
      font-style: italic;
      text-transform: uppercase;
      margin: 0 0 20px;
    }
  }
}

.address,
.contact,
.email {
  a {
    padding-left: 24px;

    &:before {
      content: '';
      position: absolute;
      height: 24px;
      width: 24px;
      top: 50%;
      left: -6px;
      background-size: contain;
      background-repeat: no-repeat;
      background-position: center;
      transform: translate(0, -50%);
    }
  }
}

.address {
  a {
    &:before {
      background-image: url(../../img/icon-GPS.svg);
    }
  }
}

.contact {
  a {
    &:before {
      background-image: url(../../img/icon-phone.svg);
    }
  }

  &__list {
    display: flex;
    flex-direction: column;

    a {
      &:not(:first-child) {
        margin-top: 10px;
      }
    }
  }
}

.email {
  a {
    &:before {
      background-image: url(../../img/icon-email.svg);
    }
  }
}