.header {
  position: fixed;
  right: 0;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 999;
  padding: 10px 0;
  background-color: $white;
  transition: 0.2s ease;

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &.is-fixed {}
}

.logo {
  width: 80px;
  height: 90px;

  img {
    height: 100%;
  }
}

.nav {

  ul {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    align-items: center;
    justify-content: center;

    li {
      margin: 0 8px;

      a {
        font-family: $main-font;
        text-decoration: none;
        font-size: 20px;
        display: inline-block;
        padding: 4px;
        color: $black;
        position: relative;

        &:after {
          content: '';
          display: block;
          width: calc(100% - 8px);
          height: 1px;
          background-color: $black;
          position: absolute;
          left: 4px;
          bottom: 4px;
          transform: scale(0);
          transform-origin: center;
          transition: 0.2s ease;
        }

        &:hover {
          @media only screen and (min-width: 1025px) {
            &:after {
              transform: scale(1);
            }
          }
        }
      }
    }
  }
}

.burger {
  height: 36px;
  width: 36px;
  display: none;

  @include max-screen(1024px) {
    display: block;
  }

  &__holder {
    position: relative;

    span {
      display: block;
      width: 100%;
      height: 2px;
      background-color: $white;
      transition: 0.3s ease;
    }
  }

  &.is-open {}
}