/* styles */
.wrapper {
	display: flex;
	flex-direction: column;
	min-height: 100vh;
	width: 100%;
	max-width: 100%;
	margin: 0 auto;
	overflow-x: hidden;
}

.header,
.footer {
	flex: 0 0 auto;
}

.main-page {
	flex: 1 0 auto;
	overflow: hidden;
}

.container {
	max-width: 1170px;
	width: 100%;
	padding: 0 10px;
	margin: 0 auto;
	color: $txt-color;
}

.section-title {
	font-size: 48px;
	font-weight: 700;
	line-height: 60px;
	margin-bottom: 20px;
}


//Hide elem 
.visually-hidden {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	bottom: 0;
	padding: 0;
	clip: rect(0 0 0 0);
	overflow: hidden;
}

.js-no-scroll {
	overflow: hidden;
}